@import url('https://kodhus.com/kodhus-ui/kodhus-0.1.0.min.css');
:root {
    --primary-color: #1DA1F2;
    --secondary-color: #E8F5FE;
    --primary-color-hover: #0b80d0;
    --body-background: #f3f5f9;
    --white-hover: #eee;
}

.profile-page-loader-container {
    width: 100%;
    height: 600px;
    display: flex;
    justify-content: center;
    background: #fff;
    padding-top: 60px;
}

.profile-data-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #fff;
    padding: 15px 90px 15px 90px;
}

.profile-data-container .friend-btn:hover {
    background: #f00!important;
}

.profile-label {
    width: 20%;
}

.form-control {
    display: inline-block;
    margin-bottom: 5px;
    width: 80%;
}

#username-taken, #email-taken {
    display: none;
    color: #f00;
}

.profile-page-menu {
    width: 100%;
    margin-top: 3px;
    background: #fff;
    color: #666;
    font-weight: bold;
    display: flex;
}

.profile-page-menu .profile-page-menu-item {
    width: 50%;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.profile-page-menu .profile-page-menu-item:hover {
    background: var(--secondary-color);
    color: var(--primary-color);
    cursor: pointer;
}

.profile-page-menu .profile-page-menu-item-active {
    color: var(--primary-color);
    border-bottom: 2px solid var(--primary-color);
}

/* POSTS */

.no-posts-container {
    width: 100%;
    height: 200px;
    background: #fff;
    margin-top: 2px
}

/* INTERESTS */

.interests-page-container {
    background: #fff;
    margin-top: 2px;
    padding: 30px;
    text-align: start;
}

.interest-item {
    width: 100%;
    min-height: 50px;
    padding: 0 30px 0 30px;
    font-size: large;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.tag-container .tag {
    width: fit-content;
    height: 30px;
    margin: 5px;
    padding: 5px 6px;
    border: 1px solid #ccc;
    border-radius: 3px;
    background: #eee;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #333;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.2), inset 0 1px 1px #fff;
    cursor: default;
}

.tag-container .tag i {
    font-size: 16px;
    color: #666;
    margin-left: 5px;
}

.tag-container input {
    padding: 5px;
    font-size: 16px;
    outline: none;
    color: #333;
    flex: 1;
}

.edit-interests-loader-container {
    width: 100%;
    height: 500px;
    background: #fff;
    display: flex;
    justify-content: center;
    padding-top: 25px;
}

.interests-title-container {
    width: 100%;
    background: #fff;
    padding: 15px;
    margin-bottom: 3px;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
}
