@import url(https://kodhus.com/kodhus-ui/kodhus-0.1.0.min.css);
:root {
    --primary-color: #1DA1F2;
    --secondary-color: #E8F5FE;
    --primary-color-hover: #0b80d0;
    --body-background: #f3f5f9;
    --white-hover: #eee;
}

.profile-page-loader-container {
    width: 100%;
    height: 600px;
    display: flex;
    justify-content: center;
    background: #fff;
    padding-top: 60px;
}

.profile-data-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #fff;
    padding: 15px 90px 15px 90px;
}

.profile-data-container .friend-btn:hover {
    background: #f00!important;
}

.profile-label {
    width: 20%;
}

.form-control {
    display: inline-block;
    margin-bottom: 5px;
    width: 80%;
}

#username-taken, #email-taken {
    display: none;
    color: #f00;
}

.profile-page-menu {
    width: 100%;
    margin-top: 3px;
    background: #fff;
    color: #666;
    font-weight: bold;
    display: flex;
}

.profile-page-menu .profile-page-menu-item {
    width: 50%;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.profile-page-menu .profile-page-menu-item:hover {
    background: #E8F5FE;
    background: var(--secondary-color);
    color: #1DA1F2;
    color: var(--primary-color);
    cursor: pointer;
}

.profile-page-menu .profile-page-menu-item-active {
    color: #1DA1F2;
    color: var(--primary-color);
    border-bottom: 2px solid #1DA1F2;
    border-bottom: 2px solid var(--primary-color);
}

/* POSTS */

.no-posts-container {
    width: 100%;
    height: 200px;
    background: #fff;
    margin-top: 2px
}

/* INTERESTS */

.interests-page-container {
    background: #fff;
    margin-top: 2px;
    padding: 30px;
    text-align: start;
}

.interest-item {
    width: 100%;
    min-height: 50px;
    padding: 0 30px 0 30px;
    font-size: large;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.tag-container .tag {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: 30px;
    margin: 5px;
    padding: 5px 6px;
    border: 1px solid #ccc;
    border-radius: 3px;
    background: #eee;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #333;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.2), inset 0 1px 1px #fff;
    cursor: default;
}

.tag-container .tag i {
    font-size: 16px;
    color: #666;
    margin-left: 5px;
}

.tag-container input {
    padding: 5px;
    font-size: 16px;
    outline: none;
    color: #333;
    flex: 1 1;
}

.edit-interests-loader-container {
    width: 100%;
    height: 500px;
    background: #fff;
    display: flex;
    justify-content: center;
    padding-top: 25px;
}

.interests-title-container {
    width: 100%;
    background: #fff;
    padding: 15px;
    margin-bottom: 3px;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
}

.profiles-filter-container {
    width: 100%;
    background: #fff;
    margin-bottom: 3px;
    padding: 15px 60px 15px 60px;
}

.profiles-filter-container .profiles-filter-input {
    width: 100%;
    height: 40px;
    padding-left: 15px;
    border: none;
    border-radius: 20px;
    background: #e3e5e9;
}

.profiles-filter-input:focus {
    outline: none;
    background: #fff;
    border: 1px solid var(--primary-color);
}

.profile-list-container {
    width: 100%;
    min-height: 500px;
    background: #fff;
    padding: 15px;
}

.filtered-profile:hover {
    background: var(--white-hover);
    cursor: pointer;
}

.profile-list-container .profile-row {
    margin-top: 2px;
}

.profile-list-container .profile-row:hover {
    background: var(--white-hover);
    cursor: pointer;
}

.profile-row img:hover, .profile-row .profile-col strong:hover {
    cursor: pointer;
    transform: scale(1.01);
}

.profile-row .profile-col {
    min-width: 20%;
    align-self: center;
    display: flex;
}

.profile-row .profile-col .main-profile-data {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.profiles-loader-container {
    width: 100%;
    height: 500px;
    display: flex;
    justify-content: center;
    padding-top: 25px;
    background: #fff;
}
.messages-wrapper {
    height: 89vh;
}

.modal-search-input {
    background: var(--body-background);
    width: 100%;
    padding: 15px;
    border: none;
    outline: none;
}

.modal-profile-li:hover {
    background: #eee;
}

.messages-loader-container {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 15px;
}

.messages-wrapper .chats-list {
    width: 35%;
    height: 100%;
    background: #fff;
    overflow: hidden;
    overflow-y: scroll;
    border-right: 3px solid var(--body-background);
}

.messages-wrapper .chats-list .search-input-container {
    display: flex;
    align-items: center;
    padding: 15px;
    border-bottom: 2px solid var(--body-background);
}

.messages-wrapper .chats-list .search-input {
    width: 100%;
    height: 40px;
    margin-right: 10px;
    border: none;
    border-radius: 20px;
    background-color: #e3e5e9;
    text-align: start;
    padding: 15px;
}

.messages-wrapper .chats-list .chats-container {
    border-radius: 0;
}

.messages-wrapper .chats-list .chats-container .profile-chat-item {
    display: flex;
    justify-content: flex-start;
    padding: 10px;
    border-bottom: 2px solid var(--body-background);
}

.messages-wrapper .chats-list .chats-container .profile-chat-item:hover {
    background: var(--white-hover);
}

.messages-wrapper .current-chat {
    width: 65%;
    height: 100%;
    background: #fff;
}

.messages-wrapper .current-chat .current-chat-header {
    display: flex;
    padding: 15px;
    width: 100%;
    border-bottom: 2px solid var(--body-background);
}

.messages-wrapper .current-chat #chat-log {
    overflow: hidden;
    overflow-y: scroll;
    width: 100%;
    padding: 15px;
}

.messages-wrapper .current-chat #chat-log .received {
    width: 100%;
    display: flex;
    justify-content: end;
}

.messages-wrapper .current-chat #chat-log .received p {
    background: #ccc;
    color: #000;
    max-width: 70%;
    padding: 15px;
    text-align: left;
    border-top-left-radius: 30px;
    border-top-right-radius: 30px;
    border-bottom-right-radius: 30px;
}

.messages-wrapper .current-chat #chat-log .sent {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    padding-left: 20px;
}

.messages-wrapper .current-chat #chat-log .sent p {
    background: var(--primary-color);
    color: #fff;
    max-width: 70%;
    padding: 15px;
    text-align: left;
    border-top-left-radius: 30px;
    border-bottom-left-radius: 30px;
    border-top-right-radius: 30px;
}

.chat-emoji-list {
    left: 30%!important;
    top: 30%!important;
}

.messages-wrapper .current-chat .send-message-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    border-top: 2px solid var(--body-background);
    padding: 3px;
}

.messages-wrapper .current-chat .send-message-container .fa-smile {
    color: var(--primary-color);
    font-size: 25px;
    margin-right: 10px;
    padding: 10px;
    border-radius: 50%;
}

.messages-wrapper .current-chat .send-message-container .fa-smile:hover {
    background: var(--secondary-color);
    cursor: pointer;
}

.messages-wrapper .current-chat .send-message-container .message-input {
    width: 70%;
    height: 40px;
    padding-left: 15px;
    border: none;
    border-radius: 20px;
    background-color: #e3e5e9;
    margin-right: 5px;
}

.messages-wrapper .chats-list .search-input:focus, .messages-wrapper .current-chat .send-message-container .message-input:focus {
    outline: none;
    background: #fff;
    border: 1px solid var(--primary-color);
}

.messages-wrapper .current-chat .send-message-container .chat-message-submit {
    height: 40px;
}

.messages-wrapper .current-chat .send-message-container .chat-message-submit .fa-paper-plane {
    position: relative;
    right: 10%;
}
@-webkit-keyframes hide {
    0% {
        opacity: 1;
        max-height: 500px;
    }
    75% {
        opacity: 0;
        max-height: 500px;
    }
    100% {
        opacity: 0;
        max-height: 0px;
    }
}

@keyframes hide {
    0% {
        opacity: 1;
        max-height: 500px;
    }
    75% {
        opacity: 0;
        max-height: 500px;
    }
    100% {
        opacity: 0;
        max-height: 0px;
    }
}

.posts-loader-container {
    width: 100%;
    height: 600px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    background: #fff;
    margin-top: 2px;
    padding-top: 60px;
}

.create-post-form {
    background: #fff;
    padding: 15px 15px 5px 15px;
}

.create-post-form .post-content-input {
    color: #000;
    border: none;
    font-size: large;
}

.create-post-form .post-img-container {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: none;
    position: relative;
    margin-top: 15px;
}

.create-post-form .post-img-container .post-form-img-preview {
    margin-top: 0!important;
}

.create-post-form .post-img-container .post-img-options {
    width: 100%;
    display: flex;
    position: absolute;
    justify-content: flex-end;
    border-radius: 20px;
    padding: 3px;
}

.create-post-form .post-img-container .post-img-options .far {
    font-size: 30px;
    color: #fff;
    background: rgba(0, 0, 0, 0.7);
    border-radius: 50%;
}

.create-post-form .post-img-container .post-img-options .far:hover {
    background: rgba(0, 0, 0, 0.2);
    cursor: pointer;
}

.post-extra-options {
    width: 20%;
    display: flex;
}

.post-extra-options .far {
    color: var(--primary-color);
    font-size: 20px;
    margin-right: 10px;
    padding: 10px;
    border-radius: 50%;
}

.post-extra-options .far:hover {
    background: var(--secondary-color);
    cursor: pointer;
}

.emoji-list-container {
    display: none;
    position: absolute;
    left: 20px;
    z-index: 1;
    border: 1px solid rgba(20, 20, 20, 0.2);
}

.post-list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}

.post-container {
    background: #fff;
    width: 100%;
    margin-top: 2px;
    display: flex;
    flex-direction: column;
    -webkit-animation-name: hide;
            animation-name: hide;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-play-state: paused;
            animation-play-state: paused;
}

.post-list .post-container:hover {
    background: var(--white-hover);
}

.post-container .post-row {
    width: 100%;
    padding: 15px;
    display: flex;
}

.post-container .post-row .post-col {
    margin-right: 10px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.post-img {
    min-width: 130px;
    min-height: 70px;
    max-width: 650px;
    max-height: 350px;
    margin-top: 15px;
    border-radius: 20px;
}

.post-container .post-actions {
    width: 25%;
    padding: 0 0 2px 79px;
    display: flex;
    justify-content: space-between;
}

.post-container .post-actions .fas {
    color: #E0245E;
}

.post-container .post-actions .fa-comment, .post-container .post-actions .fa-heart {
    padding: 10px;
    border-radius: 50%;
}

.post-container .post-actions .fa-comment:hover {
    background: var(--secondary-color);
    color: var(--primary-color);
    cursor: pointer;
}

.post-container .post-actions .fa-heart:hover {
    background: #F5E2E8;
    color: #E0245E;
    cursor: pointer;
}

.post-container .post-actions .post-likes-number {
    display: inline-block;
}

.post-container .post-actions .post-likes-number:hover {
    color: #E0245E;
    text-decoration: underline;
    cursor: pointer;
}
.comment-form {
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
}

.comment-form .chat-emoji-list {
    left: -14%!important;
    top: 100%!important;
}

.comment-form .fa-smile {
    color: var(--primary-color);
    font-size: 25px;
    margin-right: 10px;
    margin-bottom: 0!important;
    padding: 10px;
    border-radius: 50%;
}

.comment-form .fa-smile:hover {
    background: var(--secondary-color);
    cursor: pointer;
}

.comment-form input[type=text] {
    height: 37px;
}

.comment-form button {
    height: 37px;
}
:root {
  --primary-color: #1DA1F2;
  --secondary-color: #E8E5EE;
  --primary-color-hover: #0b80d0;
  --body-background: #f3f5f9;
  --white-hover: #eee;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}

@-webkit-keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

body {
  background: #f3f5f9;
  background: var(--body-background);
}

.wrapper {
  display: flex;
}

.wrapper .sidebar {
  position: fixed;
  width: 300px;
  height: 100%;
  background: #fff;
  padding: 30px 0;
}

.wrapper .sidebar h2 {
  color: #1DA1F2;
  color: var(--primary-color);
  text-align: center;
  margin-bottom: 30px;
}

.wrapper .sidebar ul li {
  color: #000;
  font-weight: bold;
  display: block;
  margin-left: 30px;
  margin-right: 30px;
  padding: 15px;
  border-radius: 20px;
}

.notifications-number {
  display: inline-block;
  position: relative;
  bottom: 10px;
  margin-bottom: 0;
  text-align: center;
  color: #fff;
  background-color: red;
  min-width: 15px;
  height: 18px;
  font-size: 12px;
  border-radius: 50%;
}

.wrapper .sidebar ul li .fas {
  width: 25px;
}

.wrapper .sidebar h2:hover {
  cursor: pointer;
}

.wrapper .sidebar ul li:hover {
  background: #E8E5EE;
  background: var(--secondary-color);
  color: #1DA1F2;
  color: var(--primary-color);
  cursor: pointer;
}

.wrapper .main-content {
  width: 100%;
  height: 100%;
  margin-left: 300px;
}

.wrapper .main-content .header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  padding: 20px;
  background: #fff;
  color: #000;
  font-size: large;
  border-bottom: 1px solid #e0e4e8;
}

.wrapper .main-content .header .logout {
  position: absolute;
  left: 80%;
  color: #1DA1F2;
  color: var(--primary-color);
  cursor: pointer;
}

.wrapper .main-content .content {
  position: relative;
  padding: 20px;
  text-align: center;
  border-left: 1px solid #e0e4e8;
}

.loader {
  width: 28px;
  height: 28px;
  background: rgba(0, 0, 0, 0);
  border: 4px solid #D2ECFC;
  border-top: 4px solid #1DA1F2;
  border-top: 4px solid var(--primary-color);
  border-radius: 50%;
  -webkit-animation: spin 0.7s linear infinite;
          animation: spin 0.7s linear infinite;
}

/* BUTTONS */

.btn-primary {
  background: #1DA1F2!important;
  background: var(--primary-color)!important;
  color: #fff!important;
  border: 1px solid rgba(0, 0, 0, 0.1)!important;
  border-radius: 20px;
  font-weight: bold!important;
}

.btn-primary:hover {
  background-color: #0b80d0!important;
  background-color: var(--primary-color-hover)!important;
  color: #fff!important;
}

.btn-secondary {
  background: rgba(0, 0, 0, 0)!important;
  color: #1DA1F2!important;
  color: var(--primary-color)!important;
  border: 1px solid #1DA1F2!important;
  border: 1px solid var(--primary-color)!important;
  border-radius: 20px!important;
  font-weight: bold!important;
}

.btn-secondary:hover {
  background: #E8E5EE!important;
  background: var(--secondary-color)!important;
}

.left-arrow-icon, .add-icon {
  padding: 10px;
  margin-right: 10px;
  color: #1DA1F2;
  color: var(--primary-color);
  border-radius: 50%;
  font-size: 20px;
}

.left-arrow-icon:hover, .add-icon:hover {
  background-color: #E8E5EE;
  background-color: var(--secondary-color);
  color: #0b80d0;
  color: var(--primary-color-hover);
  cursor: pointer;
}

.trash-icon:hover {
  color: #f00!important;
  cursor: pointer;
}

.btn-grey {
  background: #eee;
  color: #444;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 20px;
  font-weight: bold;
}

.btn-grey:hover {
  background: #eee;
  background: var(--white-hover);
}

.btn-primary:focus, .btn-secondary:focus, .btn-grey:focus {
  outline: none;
  box-shadow: none;
}

/* PROFILE-IMAGE */

.profile-img-sm {
  border-radius: 50%;
  width: 48px;
  height: 48px;
}

.profile-img-med {
  border-radius: 50%;
  width: 64px;
  height: 64px;
}

.profile-img-big {
  border-radius: 50%;
  width: 96px;
  height: 96px;
}

.profile-img-sm:hover, .profile-img-med:hover {
  transform: scale(1.01);
}

/* NOTIFICATIONS */

.notifications-container {
  width: 100%;
  height: 600px;
  background: #fff;
  padding: 15px;
}

.notifications-section {
  background: #eee;
  background: var(--white-hover);
  margin-top: 20px;
  padding: 15px;
  border-radius: 20px;
}
